@import url('https://fonts.googleapis.com/css2?family=Asap:wght@400;600;700&family=Raleway:ital,wght@0,100;0,400;0,700;0,800;1,800&display=swap');
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}



:root{
    --color-bg:#3E517A;
    --color-bg-variant:#161B33;
    --color-primary:#A8E0FF;
    --color-primary-variant:#8EE3F5;
    --color-white:#fff;
    --color-light:#F1DAC4;

    --container-width-lrg:75%;
    --container-width:86%;
    --container-width-sm:90%;
    --transition: all 400ms ease;

}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Raleway', cursive;
    background-image:url("https://img.freepik.com/free-vector/cartoon-night-nature-landscape-full-moon-shining-sky-with-stars-field-with-pond-conifer-trees-rocks-dark-heaven-with-moonlight-romantic-fantasy-background-midnight-twilight-vector-view_107791-10119.jpg?w=826&t=st=1661543157~exp=1661543757~hmac=d4c279935359547bf70f858c7c2b96019b5fa93584b8a15f62bad14a870afbeb");
    min-height:100vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color:var(--color-white);
    line-height: 1.7;
    /* background-image: ; */

  }
  
 

/* Styles */

.container{
    width: var(--container-width-lrg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}

h5{
    font-size: 2.6rem;
}

h2{
    font-size:2.9rem;
    font-family: 'Asap',sans-serif;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top:8rem;
    /* height:100vh; */
    
}

section >h2, section >h5{
    text-align: center;
    color: var(--color-light);

}

section >h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light{
    color:var(--color-light);
}

a {
    color:var(--color-primary);
    transition: var(--transition);

}

a:hover{
    color:var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border:1px solid var(--color-primary);
    transition: var(--transition);
    font-size: 1.3rem;
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background:var(--color-primary);
    color:var(--color-bg);
}
img{
    display:block;
    width:100%;
    object-fit: cover;
}

/* Tablets */
@media screen and (max-width:1024px){
    .container{
        width: var(--container-width);
    }

    section{
        margin-top: 6rem;
    }
}

/* Phones */
@media screen and (max-width:600px){
    .container{
        width: var(--container-width-sm);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}
