
  
  
  
  section > .hero-container{
    
    min-height:100vh;
     animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
      animation-fill-mode: forwards;
  }
  
  @keyframes fadeInAnimation {
      0% {
          opacity: 0;
      }
      100% {
          opacity: 1;
       }
  }
  
  .hero-section{
    font-family:'Raleway',sans-serif;
    font-size: 40px;
    color:#FFFFFF;
  }
    
  .hero-text{
    font-family:'Asap',sans-serif;
    padding-top:8rem;
    padding-left:25rem;
  }
  .hero-text > h4,h2,a{
    margin:1rem;
  }
  
  .hero-text >a{
    margin-left:10rem;
  }
  .btn{
      margin-top: 2.5rem;
      display: flex;
      gap:1.3rem;
      justify-content: center;
  }